<template>
  <div class="shopcart">
    <navigation-bar @onLeftClick="goToBack" pageName="购物车">
      <template v-slot:nav-right>
        <div class="rightTitle" :class="{active:bottomBar==1}" @click="toManageCart">编辑</div>
        <div class="rightTitle" :class="{active:bottomBar==2}" @click="allReady">完成</div>
      </template>
    </navigation-bar>
    <!--显示购物车-->
    <div class="cartContent" ref="cartContent">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">

        <div class="cartContentBox">
          <!--店铺-->
          <div class="goodsCard" v-for="(shopItem, i) in lists">
            <div class="shopCard">
              <img :src="shopItem.selectFlag==0?img.beforClick:img.afterclick" v-show="bottomBar==2"
                   @click="checkShop(i)">
              <img :src="shopItem.checkedShop==false?img.beforClick:img.afterclick" v-show="bottomBar==1"
                   @click="checkShop(i)">
              <div :class="themeCurrent == '1' ? 'shopName-l' : 'shopName'" @click="toShop(shopItem.shopCode)">
                {{shopItem.shopName}}</div>
              <img class="shopSelect" :src="img.arrowShop">
              <div :class="themeCurrent == '1' ? 'shopDeliverName-l' : 'shopDeliverName'"
                   v-if="shopItem.deliveryBaseLine!=0" @click="toShop(shopItem.shopCode)">
                满{{shopItem.deliveryBaseLine}}包邮</div>
            </div>
            <div class='actListBox' v-for="(item, j) in shopItem.actList">
              <div class="line" v-if="j!=0"></div>
              <div class="actBox" v-if="item.actCode">
                <div class="actMsgBox">
                  <div class="actTitle" v-if="item.actType==0||item.actType==1">满减</div>
                  <div class="actMsg" v-if="item.actType==0||item.actType==1">
                    {{item.actDesc}}</div>
                  <div class="actTitle" v-if="item.actType==2">打包</div>
                  <div class="actMsg" v-if="item.actType==2">{{item.actDesc}}</div>
                </div>
                <div class="actMoreBox" @click.stop="goActList(item)">
                  <div class="actMore">去凑单</div>
                  <img class="actMoreImg" src="@/assets/images/themeImg/icon_green.png" alt="">
                </div>
              </div>
              <van-swipe-cell :disabled="bottomBar==1?true:false" class="goodsDes"
                              v-for="(message, index) in item.skuList">

                <div class="secksellBox" v-if="message.seckillFlag==0||message.seckillFlag==1||message.seckillFlag==4">
                  <div class="secksellMsgBox">
                    <div class="secksellTitle">秒杀</div>
                    <div class="secksellMsg" v-if="message.seckillFlag==0||message.seckillFlag==4">该商品距离秒杀结束仅剩</div>
                    <div class="secksellMsg" v-if="message.seckillFlag==1">该商品距离秒杀开始还有</div>
                    <van-count-down :auto-start="true" class="secksellTime" :time="message.time"
                                    @finish="countDownFinish" />
                  </div>
                  <div class="secksellMoreBox" @click="goSeckSellList(message)">
                    <div class="secksellMore">去逛逛</div>
                    <img class="secksellMoreImg" src="@/assets/images/home/property_payment/more_red.png" alt="">
                  </div>
                </div>

                <div class="goodsDetailBox">
                  <div class="selectBox">
                    <img class="selectBtn" :src="message.selectFlag==0?img.beforClick:img.afterclick"
                         v-show="bottomBar==2" @click="checkSku(i,j,index)">
                    <img class="selectBtn" :src="message.checkedSku==false?img.beforClick:img.afterclick"
                         v-show="bottomBar==1" @click="checkSku(i,j,index)">
                  </div>
                  <div class="imgBox"><img class="goodsPic" :src="message.skuPicUrl" :onerror="defaultImg"
                                           @click="turnToGoods(message)">
                    <div class="lowStock"
                         v-if="message.storeNumber<=message.warningValue&&message.storeNumber!=0&&message.spuStatus!='02'&&message.seckillFlag==3">
                      仅剩{{message.storeNumber}}件</div>
                    <span class="noStore"
                          v-if="message.storeNumber==0&&message.spuStatus!='02'&&message.seckillFlag==3">补货中</span>
                    <span class="noStore" v-if="message.spuStatus=='02'">已下架</span>
                  </div>

                  <div class="goodsDetails">
                    <div :class="themeCurrent == '1' ? 'goodsName-l' : 'goodsName'" @click="turnToGoods(message)">
                      {{message.spuName}}</div>
                    <div :class="themeCurrent == '1' ? 'attrValueContent-l' : 'attrValueContent'"
                         @click="turnToGoods(message)">{{message.attrValueContent}}</div>
                    <div class="goodsDetailBottom">

                      <div :class="themeCurrent == '1' ? 'skuPrice-l' : 'skuPrice'" @click="turnToGoods(message)">
                        ￥{{message.skuPrice}}</div>

                      <div class="stepperBox">
                        <img class="subtractBtn" src="../../assets/images/mall/mall_icon_subtract.png" alt="">
                        <img class="addBtn" src="../../assets/images/themeImg/icon_add.png" alt="">
                        <van-stepper class="stepperq" v-model="message.skuCount"
                                     @blur="getCount(i,j,index,shopItem.shopCode)"
                                     @overlimit='addNumBtn(message,shopItem.shopCode)' @plus='plusBtn(i,j,index,shopItem.shopCode)'
                                     @minus='minusBtn(i,j,index,shopItem.shopCode)' :max="message.skuCountLimit"
                                     :class="{stepperThemeL:themeCurrent=='1'}"></van-stepper>
                      </div>
                    </div>

                  </div>

                </div>
                <template slot="right">
                  <button class="addCollection" type="button" @click="addCollection(message)">收藏</button>
                  <button class="delectCart" type="button"
                          @click="delectCart(shopItem.shopCode,message.skuNo)">删除</button>
                </template>
                <div class="outSecksellBox" v-if="message.seckillFlag==4">
                  <div class="outSecksellTitle"> 限{{message.seckillLimit}}件 </div>
                  <div class="outSecksellMsg">秒杀商品限购{{message.seckillLimit}}件，超出将按原价购买该商品</div>
                </div>
              </van-swipe-cell>
            </div>

            <div class="shopTotalBox">
              <div class="deliveryBox">
                <div class="deliveryTitle">配送费</div>
                <div class="deliveryNum">¥{{shopItem.delivery}}</div>
              </div>
              <div class="deliveryBox">
                <div class="deliveryTitle">包装费</div>
                <div class="deliveryNum">¥{{shopItem.packFee}}</div>
              </div>
              <div class="line"></div>
              <div class="shopTotalBottomBox">
                <div>
                  <div class="deliveryDiffNum" v-if="shopItem.deliveryDiff>0">差{{shopItem.deliveryDiff}}起送</div>
                </div>

                <div class="shopTotalNumBox">
                  <div class="shopTotalNum">¥{{shopItem.skuTotal}}</div>
                  <div :class="shopItem.skuTotal==0?'shopGotoPayGray':'shopGotoPayRed'"
                       v-if="shopItem.deliveryDiff<=0||shopItem.skuTotal==0" @click="gotoShopPay(shopItem)">去结算</div>
                  <div class="collectBill" v-if="shopItem.deliveryDiff>0&&shopItem.skuTotal!=0"
                       @click="toShop(shopItem.shopCode)">去凑单</div>
                </div>
              </div>
            </div>
          </div>

        </div>
        <netWorkError v-if="ifNetWorkError"></netWorkError>
        <!-- 购物车里无商品时显示 -->
        <div class="noList" v-if="isLists==true">
          <img :src="img.noList" />
          <div>暂无商品</div>
        </div>
        <div ref="placeHolder" v-if="isShowPlaceHolder" style="height: 300px"></div>
      </van-pull-refresh>
    </div>


    <!--</div>-->
    <!--底部功能栏-->
    <!--编辑弹窗-->
    <div class="manage" :class="{active:bottomBar==2}" :style="{paddingBottom:this.$footBarHeight+'px'}">
      <div class="checkAllBox">
        <img class="editCheckAll" :src="editCheckedAll==false?img.beforClick:img.afterclick" @click="checkAll">
        <div :class="themeCurrent == '1' ? 'editCheckAllTxt-l' : 'editCheckAllTxt'">全选</div>
      </div>
      <!-- <van-checkbox class="checkAll" v-model="checkedAll" @click="checkAll" checked-color="#17B7BD" >全选</van-checkbox> -->
      <div class="manageButtonBox">
        <button :class="themeCurrent == '1' ? 'manageDelect-l' : 'manageDelect'" @click="allDelectCart">删除</button>
        <button :class="themeCurrent == '1' ? 'manageCollect-l' : 'manageCollect'" @click="allAddCollect">加入收藏</button>
      </div>
    </div>
    <van-submit-bar :class="{active:bottomBar==1, submitThemeL:themeCurrent=='1',isClickGoodClass:isClickGood}"
                    :price="allPrice" button-text="一键结算" @submit="payForCart" :style="{paddingBottom:this.$footBarHeight+'px'}">
      <img class="checkAll" :src="checkedAll==false?img.beforClick:img.afterclick" @click="checkAll">
      <div :class="themeCurrent == '1' ? 'checkAllTxt-l' : 'checkAllTxt'">全选</div>

      <!-- <van-checkbox class="checkAll" v-model="checkedAll" @click="checkAll" checked-color="#17B7BD">全选</van-checkbox> -->
    </van-submit-bar>
    <!--补货中弹窗-->
    <div class="maskingBox" v-show="soldOut"></div>
    <div class="selloutBox" v-show="soldOut">
      <div class="selloutTxt">
        <div>补货中/已下架的商品</div>
        <div>不可结算哦</div>
      </div>
      <div class="iknowBtn" @click="iKnowBtn()">我知道了</div>
    </div>

  </div>
</template>

<style scoped="scoped" lang="scss">

</style>

  <script>
   import { mapActions, mapState } from "vuex";
  import { Dialog } from 'vant';
  import Vue from 'vue';
  import netWorkError from '../../common/networkError/NetworkError'
  import NavigationBar from '@/common/currency/NavigationBar.vue';
  import { getUserShoppingCart, getUserShoppingCartNew, delectShoppingCart, addToCollection, addCollectList, updateGoodsNumAndAct, updateGoodsSelectAndAct, updateGoodsSelect ,checkOrderValidate} from "../../services/productInfoService";
  import { store } from "../../utils/storage";

  export default {
    data: function () {
      return {
        lists: [],
        skuNum: [],
        card: '',
        count: [],
        num: 0,
        soldOut: false,//以售罄彈窗
        checkedAll: false,//完成时全选
        isCheckNum: 0, //完成时选中店铺的数量
        skuCheckNum: 0,//完成时未选中商品的数量
        editCheckedAll: false,//编辑时全选
        editIsCheckNum: 0, //编辑时选中店铺的数量
        editSkuCheckNum: 0,//编辑时未选中商品的数量
        noStore: 0,//店铺中已售完的商品数量
        skuTotalNum: 0,//商品总数量
        result: [],
        checked: false,
        allPrice: 0,
        bottomBar: 2,
        isLoading: false,
        isClickGood: true,
        isLists: false,//购物车内是否存在商品
        img: {
          beforClick: require("@/assets/images/mall/check_default.png"),
          afterclick: require("@/assets/images/themeImg/check.png"),
          arrowShop: require("@/assets/images/mall/mall_icon_more.png"),
          noList: require("@/assets/images/default/no_shopping_img.png"),
        },
      setTopHeight: { 'padding-top': this.$stbHeight + 'px' },
      isShowPlaceHolder: false,
      ifNetWorkError: false,//判断是否网络异常
      themeCurrent: this.$curTheme,
      bindThemeL: this.themeCurrent == '1' ? true : false,
      showloading: null,
      defaultImg: 'this.src="' + require('../../assets/images/default/default_picture.png') + '"',
      isInitNow: false,//是否正在刷新购物车
      }
    },
  components: {
    netWorkError,
    NavigationBar
  },
    created() {
      if (this.$deciceType=='phone'){
        require('../../assets/css/phone/product/shoppingCart.scss')
      }else if (this.$deciceType=='portraitPad'){
        require('../../assets/css/portraitPad/product/shoppingCart.scss')
      }
    },
  methods: {
  'init': function () {
    const data = {
    ownerCode: this.$userInfo.OWNER_CODE,
    areaCode: this.$userInfo.AREA_CODE,
    seckillExists: "1"
  }
    if (this.isInitNow) {
    return;
  }
    this.isInitNow = true;
  getUserShoppingCartNew(data).then(response => {
    this.isInitNow = false;
    this.ifNetWorkError = false;
    this.lists = response.data;
    if (response.data.length == 0 && this.bottomBar == 1) {
    this.bottomBar = 2;
  }

    this.checkSelect();//计算店铺选中及全选

    this.isLoading = false;
    this.isLists = response.data.length == 0 ? true : false;//判断购物车内是否存在商品
  }).catch(error => {
    //加载失败后，关闭加载等待的文字
    this.isInitNow = false;
    this.lists = [];
    this.ifNetWorkError = true;
    this.$toast("网络异常，请稍后重试");
    this.isLoading = false;
  });
  },
  'checkSelect': function () {


    var isCheckNum = 0;//店铺选中数量
    this.lists.map((item, i) => {
    item.checkedShop = false;
    item.selectFlag = 0;
    var shopSkuCheckNum = 0;
    var shopSkuCheckAndNocheckNum = 0;
    var shopSkuNum = 0;
    this.noStore = 0;

    item.actList.map((actItem) => {
    this.skuTotalNum += actItem.skuList.length;
    shopSkuNum += actItem.skuList.length;
    actItem.skuList.map((skuItem) => {

    if (skuItem.seckillFlag == 0 || skuItem.seckillFlag == 4) {
    var secondTime = skuItem.seckillEndTime - skuItem.serverTime;
    skuItem.time = secondTime * 1000;
  } else if (skuItem.seckillFlag == 1) {
    var secondTime = skuItem.seckillStartTime - skuItem.serverTime;
    skuItem.time = secondTime * 1000;
  }
    skuItem.time = secondTime * 1000;
    skuItem.checkedSku = false;
    if (!skuItem.selectFlag) {
    skuItem.selectFlag = 0;
  }
    //图片本地化存储
    Vue.prototype.$isHasLocalImage(skuItem.skuPicUrl, function (localImgUrl) {
    skuItem.skuPicUrl = localImgUrl;
  })
  if (skuItem.selectFlag == 1) {//包含补货中的商品
    shopSkuCheckNum += 1;
    shopSkuCheckAndNocheckNum += 1;
  }
    if ((skuItem.storeNumber == 0 && skuItem.seckillFlag == 3) || (skuItem.spuStatus == '02')) {//包含补货中的商品
    shopSkuCheckAndNocheckNum += 1;
  }
  });

  });
    if (shopSkuCheckAndNocheckNum == shopSkuNum) {
    isCheckNum += 1;
  }else
   {
     this.checkedAll = false;
   }
    if ((shopSkuCheckAndNocheckNum == shopSkuNum)&&shopSkuCheckNum>0) {
    item.selectFlag = 1;
  } else {
      item.selectFlag = 0;
    };

  })

  if (isCheckNum == this.lists.length) {//判断店铺是否全选中
    this.checkedAll = true;
  } else {
      this.checkedAll = false;
    }
    if (this.lists.length == 0) {
    this.checkedAll = false;
    this.editCheckedAll = false;
  }
    this.getTotal();
    this.$forceUpdate();
  },

  'onClickLeft': function () {
    this.$router.go(-1);//返回上一层
  },
    //    点击编辑
  'toManageCart': function () {

    if (this.lists.length == 0) {
    this.$toast("购物车内暂无商品哦，快去逛逛吧");
    return;
  }

    const _this = this;
    _this.bottomBar = 1;
    _this.cancelCheck();//初始化复选框状态
  },
    //    点击完成
  'allReady': function () {
    const _this = this;
    _this.bottomBar = 2;
    _this.cancelCheck();
    _this.getTotal();//重新计算商品价格
  },
    //        删除购物车内商品
  'delectCart': function (shop, sku) {
    const _this = this;
    const delectCart = {
    // ownerCode: "2019050214505753729",
    ownerCode: _this.$userInfo.OWNER_CODE,
    shopList: [{
      shopCode: shop,
      skuList: [sku],
    }]
  };
  delectShoppingCart(delectCart).then(function (response) {
    _this.init();
    // _this.cancelCheck();
    // _this.getTotal();
  });
  },
  'allDelectCart': function () {
    const _self = this;
    if (_self.lists.length == 0) {
    _self.$toast("您的购物车还没有商品呢");
  }
    const delectAllCart = {
    ownerCode: _self.$userInfo.OWNER_CODE,
    shopList: []
  };
    _self.lists.map((item, index) => {
    const skuList = [];
    item.actList.map((actItem) => {
    actItem.skuList.map((skuItem, i) => {
    if (skuItem.checkedSku == true) {
    skuList.push(skuItem.skuNo);
  }
  });
  });
    if (skuList.length != 0) {
    delectAllCart.shopList.push({
    shopCode: item.shopCode,
    skuList: skuList,
  });
  }
  });
    if (delectAllCart.shopList.length == 0) {
    _self.$toast("您还没有选中商品呢");
  } else {
    delectShoppingCart(delectAllCart).then(function (response) {
      _self.onRefresh();
    });
    }

  },
  addCollection(message) {//点击收藏切换收藏样式
    const _this = this;
    const savecollect = {
    collectType: '01',
    contentCode: message.spuCode,
    // userCode:'2019050214505753729'
    userCode: _this.$userInfo.OWNER_CODE,
  };
  addToCollection(savecollect).then(function (response) {
    if (response.data != null) {
    _this.$toast('添加收藏成功');
  } else if (response.resultCode == "50000") {
    _this.$toast('该商品已经添加收藏');
  } else {
      _this.$toast('添加收藏失败，请稍后重试');
    }
  });
  },
  'allAddCollect': function () {
    const _this = this;
    if (_this.lists.length == 0) {
    _this.$toast("您的购物车还没有商品呢");
  }
    const skuList = [];
    _this.lists.map((item, index) => {
    // const skuList = [];
    item.actList.map((actItem) => {
    actItem.skuList.map((skuItem, i) => {
    if (skuItem.checkedSku == true) {
    skuList.push({
    contentCode: skuItem.spuCode,
    collectType: "01",
    userCode: _this.$userInfo.OWNER_CODE,
  })
  }
  });
  });
  });
    if (skuList.length != 0) {
    var data = {
    collectList: skuList
  };
  addCollectList(data).then(function (response) {
    if (response.resultCode == "0") {
    _this.$toast('添加收藏成功');
  }
  });
  } else {
      _this.$toast('您还没有选中商品呢');
    }
  },
    //      全选按钮
  'checkAll': function () {
    const _this = this;
    if (_this.bottomBar == 2)//完成状态
  {
    if (_this.checkedAll == true) {
    _this.checkedAll = false;
    _this.lists.map((item, index) => {
    item.selectFlag = 0;       //先控制每个店铺的复选框为false
    item.actList.map((actItem) => {
    actItem.skuList.map((skuItem) => {
    skuItem.selectFlag = 0;     //控制每个商品的复选框为false
  });
  });
  });
    _this.updateGoodsSelectAndAct();
  } else if (_this.checkedAll == false) {
    if (_this.lists.length != 0) {
    _this.lists.map((item, index) => {
    item.actList.map((actItem) => {
    actItem.skuList.map((skuItem) => {
    if ((skuItem.storeNumber != 0 || skuItem.seckillFlag != 3) && skuItem.spuStatus != '02') {
    skuItem.selectFlag = 1;
  } else {
      skuItem.selectFlag = 0;
    }
  });
  });
    item.selectFlag = 1;
  });
    _this.checkedAll = true;
    _this.updateGoodsSelectAndAct();
  } else {
      Dialog.alert({
      message: "购物车还没有商品哦，快去逛逛吧",
      confirmButtonText: "我知道了"
    }).then(() => {
      _this.checkedAll = false;
    });
    }
  }
    // _this.getTotal();
  }
    else if (_this.bottomBar == 1)//编辑状态
  {
    if (_this.editCheckedAll == true) {
    _this.editCheckedAll = false;
    _this.lists.map((item, index) => {
    item.checkedShop = false;       //先控制每个店铺的复选框为false
    item.actList.map((actItem) => {
    actItem.skuList.map((skuItem) => {
    skuItem.checkedSku = false;     //控制每个商品的复选框为false
  });
  });
  });
  } else if (_this.editCheckedAll == false) {
    if (_this.lists.length != 0) {
    console.log(111111111111);
    _this.lists.map((item, index) => {
    item.actList.map((actItem) => {
    actItem.skuList.map((skuItem) => {
    skuItem.checkedSku = true;
  });
  });
    item.checkedShop = true;
  });
    _this.editCheckedAll = true;
  } else {
      Dialog.alert({
      message: "您的购物车暂时还没有商品呢"
    }).then(() => {
      _this.editCheckedAll = false;
    });
    }
  }
    _this.getTotal();

  }

  },
    //      选中店铺
  'checkShop': function (i) {
    const _this = this;
    if (_this.bottomBar == 2)//完成状态
  {
    if (_this.lists[i].selectFlag == 0) {
    //店铺未选中状态
    _this.lists[i].actList.map((actItem) => {
    actItem.skuList.map((skuItem) => {
    if ((skuItem.storeNumber != 0 || skuItem.seckillFlag != 3) && skuItem.spuStatus != '02') {
    skuItem.selectFlag = 1;
  } else {
      skuItem.selectFlag = 0;
    }
  });
  });
    _this.updateGoodsSelectAndAct();
    // _this.lists[i].selectFlag = 1;

    // _this.lists.map((item) => {
    //   if (item.selectFlag == 1) {
    //     _this.isCheckNum += 1;
    //   }
    // });
    // if (_this.isCheckNum == _this.lists.length) {//判断店铺是否全选中
    //   _this.checkedAll = true;
    // } else {
    //   _this.checkedAll = false;
    // }

  } else if (_this.lists[i].selectFlag == 1) {
    //店铺未选中状态

    _this.lists[i].actList.map((actItem) => {
    actItem.skuList.map((skuItem) => {
    skuItem.selectFlag = 0;
  });
  });
    _this.updateGoodsSelectAndAct();

    // _this.lists[i].selectFlag = 0;
    // _this.checkedAll = false;
  }
    //计算结算金额
    // _this.getTotal();

  }
    else if (_this.bottomBar == 1)//编辑状态
  {

    if (_this.lists[i].checkedShop == false) {
    _this.editSkuCheckNum = 0;//选中商品数量置零 ?没有计算
    _this.editIsCheckNum = 0;//选中店铺数量置零
    // _this.lists[i].skuList.map((item) => {
    //   item.checkedSku = true;
    // })
    _this.lists[i].actList.map((actItem) => {
    actItem.skuList.map((skuItem) => {
    skuItem.checkedSku = true;
  });
  });
    _this.lists[i].checkedShop = true;
    _this.lists.map((item) => {
    if (item.checkedShop == true) {
    _this.editIsCheckNum += 1;
  }
  });
    if (_this.editIsCheckNum == _this.lists.length) {//判断店铺是否全选中
    _this.editCheckedAll = true;
  } else {
      _this.editCheckedAll = false;
    }
  } else if (_this.lists[i].checkedShop == true) {
    // _this.lists[i].skuList.map((item) => {
    //   item.checkedSku = false;
    // })
    _this.lists[i].actList.map((actItem) => {
    actItem.skuList.map((skuItem) => {
    skuItem.checkedSku = false;
  });
  });
    console.log(_this.lists[i].checkedShop + "改变状态");
    _this.lists[i].checkedShop = false;
    _this.editCheckedAll = false;
  }
  }
    _this.$forceUpdate();
  },
    //选中单个商品
  'checkSku': function (i, j, index) {//i代表店铺，index代表店铺内商品
    const _this = this;
    if (_this.bottomBar == 2)//完成状态
  {

    if ((_this.lists[i].actList[j].skuList[index].storeNumber != 0 || _this.lists[i].actList[j].skuList[index].seckillFlag != 3) && _this.lists[i].actList[j].skuList[index].spuStatus != '02') {
    // if (_this.lists[i].actList[j].skuList[index].selectFlag == 0) {
    //   _this.lists[i].actList[j].skuList[index].selectFlag = 1;
    //   _this.lists.map((item) => {
    //     var shopSkuCheckNum = 0;
    //     _this.noStore = 0;
