    //       actItem.skuList.map((skuItem) => {
    //         if (skuItem.selectFlag == 1 || (skuItem.storeNumber == 0 && skuItem.seckillFlag == 3)) {//包含补货中的商品
    //           shopSkuCheckNum += 1;
