    //           }
    //         }
    //       });
    //     });
    //     if (shopSkuCheckNum == shopSkuNum && _this.noStore < shopSkuNum) {
    //       item.selectFlag = 1;
    //       _this.isCheckNum += 1;
    //     } else {
    //       item.selectFlag = 0;
    //       _this.checkedAll = false;
    //     };
    //   });
    // } else {
    //   _this.lists[i].actList[j].skuList[index].selectFlag = 0;
    //   _this.lists[i].selectFlag = 0;
    //   _this.checkedAll = false;
    //   console.log(_this.lists[i].skuList[index].selectFlag + "$$$$$$$");
    // }
    // //        从数量上判断是否全选
    // if (_this.isCheckNum == _this.lists.length) {
    //   _this.checkedAll = true;
    // } else {
    //   _this.checkedAll = false;
    // }

    if (_this.lists[i].actList[j].skuList[index].selectFlag == 0) {
    _this.lists[i].actList[j].skuList[index].selectFlag = 1;
  } else {
      _this.lists[i].actList[j].skuList[index].selectFlag = 0;
    }
    _this.updateGoodsSelectAndAct();

  } else {
      _this.soldOut = true;
      _this.stop();
    }
    // _this.getTotal();

  }
    else if (_this.bottomBar == 1)//编辑状态
  {
    _this.editIsCheckNum = 0;//选中店铺数量置零
    if (_this.lists[i].actList[j].skuList[index].checkedSku == false) {
    _this.lists[i].actList[j].skuList[index].checkedSku = true;
    _this.lists.map((item) => {
    _this.editSkuCheckNum = 0;
    // _this.noStore = 0;
    var shopSkuNum = 0;
    item.actList.map((actItem) => {
    shopSkuNum += actItem.skuList.length;
    actItem.skuList.map((skuItem) => {
    if (skuItem.checkedSku == true) {//包含补货中的商品
    _this.editSkuCheckNum += 1;
  }
  });
  });
    if (_this.editSkuCheckNum == shopSkuNum) {
    item.checkedShop = true;
    _this.editIsCheckNum += 1;
  } else {
      item.checkedShop = false;
      _this.editCheckedAll = false;
    };
  });
  } else {
      _this.lists[i].actList[j].skuList[index].checkedSku = false;
      _this.lists[i].checkedShop = false;
      _this.editCheckedAll = false;
    }
    //        从数量上判断是否全选
    if (_this.editIsCheckNum == _this.lists.length) {
    _this.editCheckedAll = true;
  } else {
      _this.editCheckedAll = false;
    }
  }

    _this.$forceUpdate();
