    if (_this.lists[i].actList[j].skuList[index].skuCount < 1) {
    _this.lists[i].actList[j].skuList[index].skuCount = 1;
  }
    if (_this.lists[i].actList[j].skuList[index].skuCount < _this.lists[i].actList[j].skuList[index].skuCountLimit) {
    // const data = {
    //   ownerCode: _this.$userInfo.OWNER_CODE,
