    //   }
    //   if (actItem.autoid && (actItem.actType == 0 || actItem.actType == 1)) {
    //     _this.allPrice -= actItem.ruleReduction * 100;
    //   }
    // });
    _this.allPrice += item.skuTotal * 100;

    if (item.skuTotal != 0 && item.deliveryDiff > 0) {
    _this.isClickGood = true;
  }

  });
    //      判断结算栏是否置灰
    if (_this.allPrice == 0) {
    _this.isClickGood = true;
  }
  },


  countDownFinish() {
    this.onRefresh();
  },

    //    购物车商品结算
  payForCart() {
    if (this.isClickGood) {
    if (this.allPrice != 0) {
    this.$toast("有店铺未满足起送价哦");
  }
    return;
  }
    const _this = this;
    const settleAccountsList = [];
    _this.lists.map((item, index) => {//循环遍历店铺
    const skuList = [];

    item.actList.map((actItem) => {
    actItem.skuList.map((skuItem, i) => {
    if (skuItem.selectFlag == 1) {
    skuList.push({
    activitySpuId: skuItem.activitySpuId,
    spuCode: skuItem.spuCode,
    spuName: skuItem.spuName,
    skuNo: skuItem.skuNo,
    attrValueContent: skuItem.attrValueContent,
    skuPrice: skuItem.skuPrice,
    skuCount: skuItem.skuCount, //数量
    skuDelivery: 0,
    skuTotal: skuItem.skuCount * skuItem.skuPrice,
    skuPicUrl: skuItem.skuPicUrl,
    // activitySpuId:_this.$route.params.activitySpuId
  })
  }
  });
  });
    if (skuList.length != 0) {
    settleAccountsList.push({
    shopName: item.shopName,
    shopCode: item.shopCode,
    skuList: skuList,
  })
  }
  });
    this.checkOrderIfValid(settleAccountsList);
    // const goodsData = {
    //   orderSource: "0",
    //   goodsList: settleAccountsList,
    //   num: Math.random() * 100
    // };
