    //    关闭弹窗
  iKnowBtn() {
    const _this = this;
    _this.soldOut = false;
    _this.move();
  },
    /***滑动限制***/
  stop() {
    var mo = function (e) { e.preventDefault(); };
    document.body.style.overflow = 'hidden';
    document.addEventListener("touchmove", mo, false);//禁止页面滑动
  },
    /***取消滑动限制***/
  move() {
    var mo = function (e) { e.preventDefault(); };
    document.body.style.overflow = '';//出现滚动条
    document.removeEventListener("touchmove", mo, false);
  },
    //键盘弹出事件
  moveInputIntoView(e) {
    console.log(this.isShowPlaceHolder);
    if (this.isShowPlaceHolder) {
    return;
  }
    this.isShowPlaceHolder = true;
    this.$nextTick(() => {
    console.log("scrollTop");
    var actualTop = document.activeElement.parentNode.parentNode.parentNode.parentNode.parentNode.parentNode.parentNode.offsetTop;
    this.$refs.cartContent.scrollTop = actualTop;
  });

  },
    //键盘收回事件
  resetInputIntoView(e) {
    var that = this;
    console.log("resetInputIntoView");
  setTimeout(function () {
    that.isShowPlaceHolder = false;
    that.$forceUpdate();
  }, 100);
    if (document.activeElement) {
    document.activeElement.blur();
  }
  },
  },

  mounted: function () {
    //添加键盘监听,让第一响应者滚动到键盘上方显示
    window.addEventListener("native.keyboardshow", this.moveInputIntoView);
    window.addEventListener("native.keyboardhide", this.resetInputIntoView);
    document.querySelector(".shopcart").addEventListener('touchmove', function (e) {
    if (document.activeElement)
    document.activeElement.blur();
  }, { passive: false });
    this.init();
  },
  beforeRouteLeave(to, from, next) {

    // this.updateSelect();
    //移除键盘监听,让第一响应者滚动到键盘上方显示
    window.removeEventListener("native.keyboardshow", this.moveInputIntoView);
    window.removeEventListener("native.keyboardhide", this.resetInputIntoView);
  next();
  },

  }
